.tooltip {
  position: fixed;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: visibility 0s 0.5s, opacity 0.5s linear;
  top: 0;
  left: 0;
}

.tooltip:before {
  background-color: black;
  opacity: 0.5;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.tooltip_open {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  transition: visibility 0s, opacity 0.5s linear;
}

.tooltip__container {
  position: relative;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
  z-index: 1;
  background-color: white;
  border-radius: 10px;

  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;

  color: #000000;

  width: 430px;
}

.tooltip__close-button {
  position: absolute;
  z-index: 1;
  background-image: url(../../images/close-icon.svg);
  background-color: transparent;
  background-size: cover;
  width: 28px;
  height: 28px;
  border: 0;
  cursor: pointer;
  top: -28px;
  left: 428px;
}
